.ContactPage .add-contact {
    background: var(--main-bg-color);
    padding: 8px;
    border-radius: 50%;
    display:block;
    position:fixed;
    bottom: 30px;
    right: 30px;
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}

.ContactPage .add-contact:hover {
    background: var(--main-special-color);
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
}


