.fixed{
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
}
.clean-list {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.clean-btn {
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
}

.clean-input{
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    outline: inherit;
}

